import React, {useContext, useEffect, useState} from 'react';
import {Route, Routes, useNavigate} from "react-router-dom";
import Sandbox from "../../SUPER/pages/Sandbox";
import CreateUsers from "../../SUPER/pages/CreateUsers";
import UsersList from "../../SUPER/pages/UsersList";
import Charts from "../../SUPER/pages/Charts";
import Sidebar from "../components/Sidebar";
import '../styles/main.css'
import {UserContext} from "../auth/auth-context";
import Dashboard from "./Dashboard";
import DeletedUsers from "../../SUPER/pages/DeletedUsers";
import Incidents from "./Incidents";
import AddIncident from "./AddIncident";
import LoadingButton from "@mui/lab/LoadingButton";
import LogoutIcon from "@mui/icons-material/Logout";
import Profile from "./Profile";
import AddDepartments from "../../SUPER/pages/AddDepartments";
import Departments from "../../SUPER/pages/Departments";
import { ring } from  "ldrs"
import { waveform } from  "ldrs"
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';


ring.register('l-ring')
waveform.register()

export const Admin = () => {

    const { currentUser, userData, baseLoading } = useContext(UserContext);
    const navigate = useNavigate()
    const [hasAccess, setHasAccess] = useState(false);
    const [loading, setLoading] = useState(false);
    const [isLoading, setIsLoading] = useState(true);

    /*useEffect(() => {
        const timer = setTimeout(() => setIsLoading(false), 5000);  // 3 seconds

        return () => clearTimeout(timer);  // Clean up on unmount
    }, []);*/

    useEffect(() => {

        if(!currentUser || !(userData?.role === 'super')) {
            setHasAccess(false);
            return navigate("/auth");
        } else {
            setHasAccess(true);
            setIsLoading(false)
        }
    }, [currentUser, navigate, userData]);

    if (isLoading || baseLoading) {
        return (
            <div className="d-flex justify-content-center align-items-center vh-100 text-bg-light">
                <l-waveform
                    size="45"
                    stroke="6"
                    speed="3.4"
                    color="purple"
                ></l-waveform>
            </div>
        )
    }

    const handleToggleSidebar = () => {
        document.body.classList.toggle('toggle-sidebar');
    };

    const loginClick = async (e) => {
        e.preventDefault()
        setLoading(true);
        try {
            setTimeout(() => {
                setLoading(false)
                navigate('/auth')
            }, 1500)
        } catch (e) {
            alert(e.message)
            setLoading(false)
        }
    }

    return (
        <div className={`hub container-fluid`}>
            { hasAccess &&(
                <div className={`base row vh-100 sticky-bottom`}>
                    <div className="">
                        <Sidebar
                        isAdmin={true}
                        />
                    </div>
                    <div className={`main col with-sidebar`}>{/*<div className={`main col ${currentUser ? 'with-sidebar' : ''}`}>*/}
                        <div className="sub">
                            <Routes>
                                <Route path={'/'} element={<Dashboard />} />
                                <Route path={'profile'} element={<Profile />} />
                                <Route path={'create-user'} element={<CreateUsers />} />
                                <Route path={'users'} element={<UsersList />} />
                                <Route path={'deleted-users'} element={<DeletedUsers />} />
                                <Route path={'incidents'} element={<Incidents />} />
                                <Route path={'add-incident'} element={<AddIncident />} />
                                <Route path={'sand'} element={<Sandbox/>} />
                                <Route path={'departments'} element={<Departments />} />
                                <Route path={'add-departments'} element={<AddDepartments />} />
                                <Route path={'charts'} element={<Charts />} />
                            </Routes>
                            <div className={`btn-sidebar btn btn-primary align-self-start  bottom-0 start-0 m-3 sticky-bottom`}
                                 htmlFor={`sidebar1`}
                                 onClick={handleToggleSidebar}
                            > Test </div>
                        </div>

                    </div>
                </div>)}
            { !hasAccess &&(
                <div className={`base row vh-100 sticky-bottom`}>
                    <LoadingButton
                        startIcon={<LogoutIcon/>}
                        variant="contained"
                        color="error"
                        loading={loading}
                        loadingPosition="start"
                        onClick={loginClick}>
                        <span>Login</span>
                    </LoadingButton>
                </div>)}
        </div>
    );
}

export const User = () => {
    const navigate = useNavigate()
    const { currentUser, userData, baseLoading } = useContext(UserContext);
    const [hasAccess, setHasAccess] = useState(false);
    const [loading, setLoading] = useState(false);
    const [isLoading, setIsLoading] = useState(true);
    /*useEffect(() => {
        const timer = setTimeout(() => setIsLoading(false), 5000);  // 3 seconds

        return () => clearTimeout(timer);  // Clean up on unmount
    }, []);*/

    useEffect(() => {
        if(!currentUser || !(userData?.role === 'user')) {
            setHasAccess(false);
            return navigate("/auth");
        } else {
            setHasAccess(true);
            setIsLoading(false)
        }
    }, [currentUser, navigate, userData]);

    if (isLoading || baseLoading) {
        return (
            <div className="d-flex justify-content-center align-items-center vh-100 text-bg-light">
                <l-waveform
                    size="45"
                    stroke="6"
                    speed="3.4"
                    color="purple"
                ></l-waveform>
            </div>
        )
    }
    const loginClick = async (e) => {
        e.preventDefault()
        setLoading(true);
        try {
            setTimeout(() => {
                setLoading(false)
                navigate('/auth')
            },1500)
        } catch (e) {
            alert(e.message)
            setLoading(false)
        }
    }

    return (
        <div className={`hub container-fluid`}>
            { hasAccess &&(
                <div className={`base row`}>
                    <Box sx={{ flexGrow: 1 }}>
                        <AppBar position="static" className={"AppBar"}>
                            <Toolbar variant="dense">
                                <IconButton edge="start" color="inherit" aria-label="menu" sx={{ mr: 2 }}>
                                    <MenuIcon />
                                </IconButton>
                                <Typography variant="h6" color="inherit" component="div">
                                    Photos
                                </Typography>
                            </Toolbar>
                        </AppBar>
                    </Box>
                    <div className="">
                        <Sidebar
                        isAdmin={false}
                        />
                    </div>
                    <div className={`main col with-sidebar`}>{/* <div className={`main col ${currentUser ? 'with-sidebar' : ''}`}>*/}
                        <div className="sub">
                            <Routes>
                                <Route path={'/'} element={<Dashboard />} />
                                <Route path={'/profile'} element={<Profile />} />
                                <Route path={'/incidents'} element={<Incidents />} />
                                <Route path={'/add-incident'} element={<AddIncident />} />
                            </Routes>
                        </div>
                    </div>
                </div>)}
            { !hasAccess &&(
                <div className={`base row vh-100 sticky-bottom`}>
                    <LoadingButton
                        startIcon={<LogoutIcon/>}
                        variant="contained"
                        color="error"
                        loading={loading}
                        loadingPosition="start"
                        onClick={loginClick}>
                        <span>Login</span>
                    </LoadingButton>
                </div>)}
        </div>
    );

}
