import {useContext, useEffect, useState} from "react";
import { auth } from "../config/firebase";
import {useNavigate} from "react-router-dom";
import {UserContext} from "./auth-context";
import {host} from "../config/api-config";
import LoadingButton from "@mui/lab/LoadingButton";
import LoginIcon from "@mui/icons-material/Login";
import { waveform } from  "ldrs"

waveform.register()

const Login = () => {
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const navigate = useNavigate()
    const [loading, setLoading] = useState(false)
    const [error, setError] = useState("");
    const [isLoading, setIsLoading] = useState(true);



    const { signIn, currentUser, userData, baseLoading } = useContext(UserContext);

    useEffect(() => {
        if (currentUser && userData) {
            console.log('currentUser-Login.jsx');
            console.log(currentUser);
            console.log('userData-Login.jsx');
            console.log(userData);
            // Handle role-based redirection
            switch (userData.role) {
                case 'user':
                    setIsLoading(false)
                    navigate("/user");
                    break;
                case 'super':
                    setIsLoading(false)
                    navigate("/admin");
                    break;
                default:
                    setIsLoading(false)
                    navigate("/auth");
                    console.log("Unknown role");
            }
            setIsLoading(false)

        }
        setIsLoading(false)
    }, [currentUser, navigate, userData]);

    if (!(!isLoading && !baseLoading)) {
        return (
            <div className="d-flex justify-content-center align-items-center vh-100 text-bg-light">
                <l-waveform
                    size="45"
                    stroke="6"
                    speed="3.4"
                    color="purple"
                ></l-waveform>
            </div>
        )
    }

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            setError("")
            setLoading(true)
            console.log(email, password)
            await signIn(email, password)
            console.log(auth?.currentUser);
            const userId = auth?.currentUser?.uid;
            const url = `${host}/users/get-user-role/${userId}`
            fetch(url, {})
                .then(response => response.json())
                .then(data => {
                    if (data.status === 'success') {
                        // Handle role-based redirection
                        setLoading(false)
                        switch (data.role) {
                            case 'user':
                                navigate("/user");
                                break;
                            case 'super':
                                navigate("/admin");
                                break;
                            default:
                                navigate("/auth");
                                console.log("Unknown role");
                        }

                    } else {
                        setLoading(false)
                        console.error('Error retrieving user role:', data.message);
                    }

                })
                .catch(error => {
                    console.error('Dot Catch Error:', error);
                    setLoading(false)
                });
        } catch (e) {
            setError("Failed to log in")
            console.error(e)
            alert(error)

        }
        setLoading(false)
    }




    return(
        <div className="container-fluid ">
            <div className="row justify-content-center align-items-center min-vh-100 body-bg-tertiary">
                <div className="col-12 col-md-6 col-lg-4 mx-auto my-16 p-4 border text-bg-light">
                    {/*{loading && (
                        <div className="position-absolute inset-0 bg-dark bg-opacity-75 d-flex justify-content-center align-items-center">
                            <HashLoader color="white" size={40} />
                        </div>
                    )}*/}
                    <div className="card-body ">
                        <form id="login-form">
                            <h1 className="text-center text-3xl text-bg-light">Login</h1>
                            <div className="mb-3">
                                <label htmlFor="inputEmail" className="form-label text-bg-light">
                                    Email Address
                                </label>
                                <input
                                    type="email"
                                    className="form-control text-bg-light border border-dark border-3"
                                    id="inputEmail"
                                    aria-describedby="emailHelp"
                                    onChange={(e) => setEmail(e.target.value)}
                                    style={{ borderRadius: '20px' }}
                                />
                            </div>
                            <div className="mb-3">
                                <label htmlFor="inputPassword" className="form-label text-bg-light">
                                    Password
                                </label>
                                <input
                                    type="password"
                                    className="form-control text-bg-light border border-dark border-3"
                                    id="inputPassword"
                                    onChange={(e) => setPassword(e.target.value)}
                                    style={{ borderRadius: '20px' }}
                                />
                            </div>
                            <div className="text-center">
                                <LoadingButton
                                    /*style={{ borderRadius: '5px' }}*/
                                    type="submit"
                                    endIcon={<LoginIcon />}
                                    variant="contained"
                                    color="secondary"
                                    loading={loading}
                                    loadingPosition="end"
                                    onClick={handleSubmit}>
                                    <span>Login</span>
                                </LoadingButton>
                                <div className="mb-3"></div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Login
