import React, {useEffect, useState} from "react";
import Button from "@mui/material/Button";
import DeleteIcon from '@mui/icons-material/Delete'
import EditIcon from '@mui/icons-material/Edit'
import { ring } from  "ldrs"
import {IconButton} from "@mui/material";
import Chip from "@mui/material/Chip";
import Stack from "@mui/material/Stack";




ring.register('l-ring')



const ListCards = (props) => {
    // Assuming that you have an array of user objects in the 'props.users' prop.
    const users = props.users;

    const list = [
        {field:'Email', data:`email`},
        {field:'ID', data:'employeeId'},
        {field:'Position', data:'position'},
        {field:'Department', data:'department'},
        {field:'Role', data:'role'},
        {field:'Access Level', data:'access'}
    ]

    const [editingUser, setEditingUser] = useState(null);
    const [userEdits, setUserEdits] = useState({});

    const handleEditClick = user => {
        setEditingUser(user.id);
        setUserEdits(user);
    };

    const handleChange = (field, value) => {
        setUserEdits(prevState => ({
            ...prevState,
            [field]: value
        }));
    };

    const saveEdits = () => {
        props.updateUser(userEdits);
        setEditingUser(null);
    };

    const cancelEdit = () => {
        setEditingUser(null);
    };

    return (
        <>
                {users.map((user, index) => (
                    <div key={index} className={`col-12 overflow-hidden`}>
                        <div  key={user.id} className="card mb-4 border rounded rounded-4 shadow-lg">

                            <div className="card-header justify-content-between bg-dark-subtle border-0">
                                <h5 className="col-10">{user.firstName.toUpperCase()} {user.lastName.toUpperCase()}</h5>
                                <div className="col d-flex justify-content-between">
                                    {/*<button className="btn btn-primary" onClick={() => props.viewUser(user['id'])}>View</button>*/}
                                    {!(editingUser === user.id) &&
                                        <Button variant="contained"
                                                color="secondary"
                                                endIcon={<EditIcon />}
                                                onClick={() => handleEditClick(user)}>Edit</Button>
                                    }
                                    {editingUser === user.id &&
                                        <Button variant="outlined"
                                                color="info"
                                                onClick={async () => setEditingUser(await props.updateUser(user['id'], userEdits))}>Save</Button>
                                    }
                                    {editingUser === user.id &&
                                        <Button variant="outlined"
                                                color="warning"
                                                onClick={() => cancelEdit()}>Cancel</Button>
                                    }
                                    {/*{editingUser === user.id &&
                                        <l-ring
                                            className="border border-danger border-1"
                                            size="40"
                                            stroke="5"
                                            bg-opacity="0"
                                            speed="2"
                                            color="black"
                                        />
                                    }*/}
                                    <Button
                                        variant="contained"
                                        color="error"
                                        startIcon={<DeleteIcon />}
                                        onClick={() => props.deleteUser(user['id'])}>Delete
                                    </Button>


                                </div>
                            </div>

                            <div className="card-body border-0 overflow-hidden">
                                {/*<h5 className="card-title">{user.firstName} {user.lastName}</h5>*/}
                                <Stack className="card-text p-1" direction="row" spacing={1}>
                                    {list.map((item, index) => (
                                        <>
                                            { item.field !== '' &&
                                                <Chip className={`col shadow`} color={(user[item.data] === 'critical'? "error": "primary")} key={index} label={item.field === 'Department'? user[item.data].name : user[item.data]} />
                                            }
                                        </>
                                    ))}
                                </Stack>
                                {/*{list.map((item, index) => (
                                    <div key={index} className="card-text bg-dark-subtle m-3 p-3 rounded-2 col">
                                    <p className="card-text p-1 fw-bold">{item.field}</p>

                                        {editingUser === user.id ?
                                            <input
                                                className={`card-text p-1 ${editingUser ? '' : 'border border-0'}`}
                                                value={userEdits[item.data]}
                                                onChange={event => handleChange(item.data, event.target.value)}
                                            /> :
                                            <p className="card-text p-1">{item.field === 'Department'? user[item.data].name : user[item.data]}</p>
                                        }
                                    </div>
                                ))}*/}

                            </div>
                        </div>
                    </div>
                ))}
        </>
    );
};

export default ListCards;
