import React, {useState} from "react";
import Button from "@mui/material/Button";
import DeleteIcon from '@mui/icons-material/Delete'
import { styled } from '@mui/material/styles';
import CheckIcon from '@mui/icons-material/Check'
import EditIcon from '@mui/icons-material/Edit'
import { ring } from  "ldrs"
import IconButton from "@mui/material/IconButton";
import UploadFileIcon from '@mui/icons-material/UploadFile';
import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import IncidentDetailsPopup from "./IncidentDetailsPopup";
import Chip from '@mui/material/Chip';
import Stack from '@mui/material/Stack';





ring.register('l-ring')



const IncidentCards = (props) => {
    const user = props.user;
    const incidents = props.incidents;
    const departments = props.departments;
    const employees = props.employees;
    const [department, setDepartment] = useState({})
    const [departmentList, setDepartmentList] = useState([])
    const [showPopup, setShowPopup] = useState(false);
    const [popupIncident, setPopupIncident] = useState(null);
    const list = [
        {field:'ID', data:'id'},
        {field:'Title', data:'title'},
        {field:'Description', data:'description'},
        {field:'Incident Type', data:'incidentType'},
        {field:'Severity', data:'severity'},
        {field:'Status', data:'status'}
    ]
    const [assigningIncident, setAssigningIncident] = useState(null);
    const [assignedTo, setAssignedTo] = useState({});
    const [recommendation, setRecommendation] = useState('');

    const handleCardClick = (incident) => {
        setPopupIncident(incident);
        setShowPopup(true);
    };

    const handleStatusColor = (incident) => {
        switch (incident.status) {
            case "unassigned":
                return "error"

            case "pending":
                return"warning"

            case "ongoing":
                return"secondary"

            case "completed":
                return"success"

            default:
                return ""
        }
    }

    const handleClosePopup = () => {
        setShowPopup(false);
    };

    const handleRecommendationChange = e => {
        setRecommendation(e.target.value);
    };

    const handleAssignClick = incident => {
        setAssigningIncident(incident.id);
        setDepartmentList(departments)
        setAssignedTo(incident.assignedTo)
    };

    const handleConfirm = (id) => {
        props.assignIncident(id, assignedTo, recommendation);
        setAssigningIncident(null);
        setAssignedTo([]);
        setRecommendation('');
    };

    const cancelEdit = () => {
        setAssigningIncident(null);
        setAssignedTo([]);
        setRecommendation('');
    };

    return (
        <>
                {incidents.map((incident, index) => (
                    <div key={index} className={`col-12 container-fluid`} >
                    <div  key={incident.id} className={`card mb-4 border rounded rounded-4 `} onDoubleClick={() => handleCardClick(incident)}>

                        <div className="card-header justify-content-between border-0">
                            <h3 className="col-10"  onDoubleClick={() => handleCardClick(incident)}>{incident.title}</h3>
                            <div className="col d-flex justify-content-between">
                                {(((assigningIncident === incident.id)  && user.access === 'level2') && incident.status === 'unassigned') &&
                                    <div className={'d-flex flex-row justify-content-between'}>

                                        <div className="m-2 form-floating">
                                            <select className="form-select" placeholder={"Select Department"}
                                                    id="departmentDropDown"
                                                     aria-label="Default select example"
                                                    onChange={(e) => {
                                                        const newDepartment = departmentList.find(department => department.id === e.target.value);
                                                        setDepartment(newDepartment)
                                                        setAssignedTo({
                                                            department: newDepartment,
                                                            employee: {
                                                                "name": "unassigned",
                                                                "id": "unassigned"
                                                            }})
                                                    }}>
                                                <option value="">Select Department</option>
                                                {departmentList.map(department => (
                                                    <option value={department.id}
                                                            key={department.id}>{department.name}</option>
                                                ))}
                                            </select>
                                            <label htmlFor="departmentDropDown"
                                                   className="form-label">Department</label>
                                        </div>

                                        <input
                                            className="form-control m-2"
                                            value={recommendation}
                                            onChange={handleRecommendationChange}
                                            placeholder="Enter recommendation"
                                        />
                                        <Button variant="contained" color="error"
                                                onClick={() => cancelEdit()}>Cancel</Button>
                                        <Button variant="contained" color="info"
                                                onClick={() => handleConfirm(incident.id)}>Confirm</Button>
                                    </div>
                                }
                                {incident.status !== 'unassigned' &&
                                    <Stack className="card-text p-1" direction="row" spacing={1}>
                                        <Chip className={`col shadow`} color={handleStatusColor(incident)} label={incident.status} />
                                    </Stack>
                                }
                                {(!(assigningIncident === incident.id) && incident.status !== 'completed') &&
                                    <>
                                    {
                                        (user.access === 'level2' && incident.status === 'unassigned') &&

                                        <Button
                                            variant="contained"
                                            color="secondary"
                                            startIcon={<EditIcon />}
                                            onClick={() => handleAssignClick(incident)}
                                        >
                                            Assign
                                        </Button>
                                    }

                                        <Button
                                            variant="contained"
                                            color="success"
                                            size="small"
                                            endIcon={<CheckIcon />}
                                            onClick={() => props.closeIncident(incident.id)}>
                                            Mark As Completed
                                        </Button>
                                    </>
                                }
                            </div>
                        </div>

                        <div className="card-body border-0 overflow-hidden">
                            <Stack className="card-text p-1" direction="row" spacing={1}>
                                {list.map((item, index) => (
                                    <>
                                        { item.field !== '' &&
                                            <Chip className={`col shadow`} color={(incident[item.data] === 'critical'? "error": "primary")} key={index} label={incident[item.data]} />
                                        }
                                    </>
                                ))}
                            </Stack>
                            {/*{list.map((item, index) => (
                                <>
                                    { item.field !== '' &&
                                        <div key={index} className="card-text bg-dark-subtle m-3 p-3 rounded-2 col">
                                            <p className="card-text p-1">
                                                {item.field === 'Created By' ? `${(incident[item.data]).name}` :
                                                    item.field === 'Assigned To' ?
                                                        (<>
                                                            {`Department: ${(incident[item.data]?.department)?.name || 'Unassigned'}`}
                                                            <br/>
                                                            {`Employee: ${(incident[item.data]?.employee)?.name || 'Unassigned'}`}
                                                        </>) :
                                                        incident[item.data]
                                                }
                                            </p>
                                        </div>
                                    }
                                </>
                            ))}*/}
                        </div>
                    </div>
                    </div>
                ))}

                {showPopup && popupIncident &&
                    (<div className="">
                        <IncidentDetailsPopup
                            incident={popupIncident}
                            onClosePopup={handleClosePopup}
                            open={showPopup}
                        />
                    </div>
                )}
        </>
    );
};

export default IncidentCards;
