// Importing required hooks and components
import React, {useContext, useEffect, useState} from "react";
import DashCards from "../components/DashCards";
import DashTable from "../components/DashTable";
import {host} from "../config/api-config";
import {UserContext} from "../auth/auth-context";
import DashLinks from "../../SUPER/components/DashLinks";
import { waveform } from  "ldrs"

waveform.register()

// Dashboard component.
// This component represents a user's dashboard, including their incident statistics and list of incidents.
const Dashboard = () => {
    const { currentUser, userData, baseLoading } = useContext(UserContext);
    const [isAdmin,setIsAdmin] = useState(false)
    const [departments, setDepartments] = useState([]);
    const [isLoading, setIsLoading] = useState(true);
    const depUrl = `${host}/departments/get-departments`
    const [incidentStats, setIncidentStats] = useState([
        { name: 'Ongoing', num: 0 },
        { name: 'Completed', num: 0 },
        { name: 'Unassigned', num: 0 },
        { name: 'Total', num: 0 },
    ]);
    const [userIncidentStats, setUserIncidentStats] = useState([
        { name: 'Ongoing', num: 0 },
        { name: 'Completed', num: 0 },
        { name: 'Unassigned', num: 0 },
        { name: 'Total', num: 0 },
    ]);
    const parameters = {
        id: currentUser?.uid,
        departmentId: userData?.department.id,
        access: userData.access
    }
    const url = `${host}/incidents/get-incident-stats`;
    const userUrl = `${host}/incidents/get-user-incident-stats`;
    let query = objectToQueryString(parameters);
    let urlWithQuery = userUrl + query;
    const [list2, setList2] = useState([
        {
            name: `Turbine Failure`,
            id: 21347,
            department: "Engineering",
            status: "Pending",
            progress: "50%"
        },
        {
            name: `Turbine Failure`,
            id: 21347,
            department: "Engineering",
            status: "Pending",
            progress: "50%"
        },
        {
            name: `Turbine Failure`,
            id: 21347,
            department: "Engineering",
            status: "Pending",
            progress: "50%"
        },
        {
            name: `Turbine Failure`,
            id: 21347,
            department: "Engineering",
            status: "Pending",
            progress: "50%"
        },
        {
            name: `Turbine Failure`,
            id: 21347,
            department: "Engineering",
            status: "Pending",
            progress: "50%"
        },
        {
            name: `Turbine Failure`,
            id: 21347,
            department: "Engineering",
            status: "Pending",
            progress: "50%"
        }
    ])



    // Function to convert a string to title case.
    function toTitleCase(str) {
        return str
            .split(' ')
            .map(word => word.charAt(0).toUpperCase() + word.slice(1).toLowerCase())
            .join(' ');
    }

    function objectToQueryString(obj) {
        return "?" + Object.keys(obj).map(key => key + "=" + obj[key]).join("&");
    }

    // Effect to fetch user data and incident stats when the userId changes.
    useEffect(() => {
        const fetchIncidentStats = async () => {
            const response = await fetch(url);
            const data = await response.json();
            if (data.status === 'success') {
                const newIncidentStats = [
                    {name: 'Ongoing', num: data.ongoingCount},
                    {name: 'Completed', num: data.completedCount},
                    {name: 'Unassigned', num: data.unassignedCount},
                    {name: 'Total', num: data.totalCount},
                ];
                setIncidentStats(newIncidentStats);
                setIsLoading(false)
            } else {
                console.error(`Failed to fetch incident stats: ${data.message}`);
            }
        };
        const fetchUserIncidentStats = async () => {
            const response = await fetch(urlWithQuery, {
                method: 'GET', // or 'POST'
                headers: {
                    'Content-Type': 'application/json'
                }
            });
            const data = await response.json();
            if (data.status === 'success') {
                const newIncidentStats = [
                    {name: 'Ongoing', num: data.ongoingCount},
                    {name: 'Completed', num: data.completedCount},
                    {name: 'Unassigned', num: data.unassignedCount},
                    {name: 'Total', num: data.totalCount},
                ];
                setUserIncidentStats(newIncidentStats);
                setIncidentStats(newIncidentStats);
                setIsLoading(false)
            } else {
                console.error(`Failed to fetch incident stats: ${data.message}`);
            }
        };
        const FetchDepartments = async () => {
            const response = await fetch(depUrl)
            const data = await response.json();

            if (data.status === 'success') {
                console.log(data);
                const newDepartments = []
                data.data.map((item, index) => (
                    newDepartments.push({name: (item.name).toUpperCase(), path: item.name})
                ))
                setDepartments(newDepartments);
            } else {
                console.error(`Failed to fetch departments: ${data.message}`);
            }
        };

        if(userData.role === 'super' || userData.access === 'level2'){
            setIsAdmin(true)
            fetchIncidentStats();
        }
        else {
            fetchUserIncidentStats();
        }

        FetchDepartments();
    }, [currentUser ]);

    if (isLoading || baseLoading) {
        return (
            <div className="d-flex justify-content-center align-items-center vh-100 text-bg-light">
                <l-waveform
                    size="45"
                    stroke="6"
                    speed="3.4"
                    color="purple"
                ></l-waveform>
            </div>
        )
    }

    // Placeholder list, presumably to be replaced with fetched data.


    // Placeholder CSS classes, presumably to be replaced with actual classes.
    const b1 = "";
    const b2 = "";
    const b3 = "";

    return(
        <div className={`container-fluid `}>
            <h1 className={`row`}>
                {isAdmin?'Admin':toTitleCase(userData.department.name)} Dashboard
            </h1>
            {(userData.role === 'user') &&
                (<DashCards
                     map={incidentStats}
                />)}
            {(userData.role === 'user') &&
                (<DashTable
                    map={list2}
                 />)
            }
            {(userData.role === 'super') &&
                (<DashCards
                     map={incidentStats}
                />)}
            {(userData.role === 'super') &&
                (<DashLinks
                    list={departments}
                />)
            }
        </div>
    )
}
export default Dashboard;