import{
    onAuthStateChanged,
    signInWithPopup,
    signInWithEmailAndPassword,
    signOut
} from "firebase/auth";
import {createContext, useState, useEffect} from "react";
import { auth, googleProvider} from "../config/firebase";
import {host} from "../config/api-config";


export const UserContext = createContext();


const UserProvider = ({children}) => {
    const [currentUser, setCurrentUser] = useState(null);
    const [userData, setUserData] = useState(null);
    const [baseLoading, setBaseLoading] = useState(true);
    const isLive = auth?.currentUser


    useEffect(() => {

        const unsub = onAuthStateChanged(auth, user => {
            const fetchUserData = async (user) => {
                try {
                    setCurrentUser(user)
                    const response = await fetch(`${host}/users/get-user/${user.uid}`);
                    const data = await response.json();
                    if (data.status === 'success') {
                        await setUserData(data.data);
                        console.log('fetchUserData in auth-context:')
                        console.log('data.data:')
                        console.log(data.data)
                        console.log('userData:')
                        console.log(userData);
                        setBaseLoading(false)
                    }
                } catch (error) {
                    console.error(`Failed to fetch user data: ${error}`);
                    setBaseLoading(false)

                }
            };
            // Fetch additional user data

             fetchUserData(user)

            console.log(`unsub(onAuthStateChanged) in auth-context:`)
            console.log(`   user:`)
            console.log(user)
            console.log('   currentUser:')
            console.log(currentUser)
        });


        // Cleanup subscription on unmount
        return () => {
            unsub();
        }
    }, []);

    const signIn = (email, password) => {
        return signInWithEmailAndPassword(auth, email, password)
    }
    const googleSignIn = () => {
        return signInWithPopup(auth, googleProvider)
    }
    const logOut = () => {
        return  signOut(auth)
    }

    return(
        <UserContext.Provider value={{currentUser, userData, baseLoading, signIn, googleSignIn, logOut}}>
            {children}
        </UserContext.Provider>
    )
};


export default UserProvider;

